html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  color: #1a1b15;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  height: 100%;
  background-color: #e1e1e1;
}
@media (max-width: 480px) {
  body {
    min-height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

/*Componenetes*/
.alerta {
  padding: 0.8333333333rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

.boton-azul {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
}
@media (max-width: 768px) {
  .boton-azul {
    width: 100%;
  }
}
.boton-azul:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
.boton-azul:hover {
  background-color: #054362;
}

