/*prueba*/
body {
  background-color: #a5a5a5;
}

.contenedor_principal {
  margin: 2rem 2rem 0rem 2rem;
  border-radius: 1.5rem;
  background-color: #191821;
}
.contenedor_principal h2 {
  color: white;
  padding-bottom: 3rem;
}

.nav_principal {
  display: block;
  padding: 0rem 4rem;
  justify-content: space-between;
  text-transform: uppercase;
  font-weight: bold;
  align-items: center;
  margin-bottom: 4rem;
}
@media (min-width: 550px) {
  .nav_principal {
    display: flex;
  }
}
.nav_principal a {
  color: white;
  padding: 2rem;
  text-align: center;
}
.nav_principal a:hover {
  background-color: rgb(45, 46, 44);
  border-radius: 1rem;
}
.nav_principal .solo {
  font-size: 3rem;
}
.nav_principal .solo a {
  padding: 1rem;
}
@media (max-width: 550px) {
  .nav_principal .solo {
    margin-top: 2rem;
  }
}

.nav_secundario {
  display: flex;
  flex-direction: column;
}
.nav_secundario a {
  padding: 0.5rem;
}
@media (min-width: 860px) {
  .nav_secundario {
    display: flex;
    flex-direction: row;
    gap: 3rem;
    font-size: 1.5rem;
  }
}

.solo {
  display: flex;
  justify-content: center;
}

.footer {
  margin: 2rem 2rem 0rem 2rem;
  border-radius: 1rem;
  background-color: rgb(46, 43, 43);
}
.footer p {
  color: white;
  font-size: 1.5rem;
  text-align: center;
}

.opciones {
  margin: 6rem 0rem 1rem 0rem;
  background-color: transparent;
  color: white;
  border: none;
  border-radius: 2rem;
  text-align: center;
}
@media (min-width: 860px) {
  .opciones {
    margin: 0;
  }
}
@media (max-width: 550px) {
  .opciones {
    margin: 2rem;
  }
}
.opciones .opprimero {
  background-color: #191821;
}

.nombreP {
  color: white;
  background-color: #6e6e6e;
  display: flex;
  justify-content: center;
  padding: 0.5rem;
  margin: 4rem;
  max-width: 16rem;
  border-radius: 0.5rem;
}

.articulo_general {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  margin: 0rem 2rem 0rem 2rem;
}
@media (min-width: 980px) {
  .articulo_general {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}

.articulo_izquierda {
  height: 50rem;
  background-image: url(/imagenes/donacionorganos.jpg);
  background-position: center;
  background-size: cover;
  border-radius: 1.5rem;
  background-repeat: no-repeat;
}
.articulo_izquierda .contenido_articulo {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}
.articulo_izquierda .contenido_articulo h2 {
  margin-top: 2rem;
}
.articulo_izquierda .contenido_articulo p {
  background-color: rgb(122, 121, 121);
  color: white;
  padding: 2rem;
  margin: 1rem;
  border-radius: 1.5rem;
  font-weight: bold;
  display: block;
}

.articulo_derecha {
  height: 50rem;
  background-image: url(/imagenes/donaciondeorganos.jpg);
  background-position: center;
  background-size: cover;
  border-radius: 1.5rem;
  background-repeat: no-repeat;
}
.articulo_derecha p {
  background-color: rgb(174, 185, 74);
  color: white;
  padding: 2rem;
  margin: 1rem;
  border-radius: 1.5rem;
  display: flex;
  font-weight: bold;
}
.articulo_derecha .contenido_articulo {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}
.articulo_derecha h4 {
  text-align: center;
  color: white;
}

.contenido_medio {
  display: block;
  background-color: #616a77;
  margin: 2rem;
  padding: 2rem;
  gap: 2rem;
  border-radius: 1.5rem;
}
@media (min-width: 600px) {
  .contenido_medio {
    display: block;
  }
  .contenido_medio img {
    max-width: 20px;
  }
}
@media (min-width: 980px) {
  .contenido_medio {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.contenido_izquierdo {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contenido_izquierdo h2 {
  font-weight: normal;
  align-items: flex-start;
  justify-content: start;
}
.contenido_izquierdo p {
  font-size: 2rem;
  line-height: 2.8rem;
  font-weight: bold;
}

.contenido_derecho {
  display: flex;
  justify-content: center;
}
.contenido_derecho img {
  border-radius: 5rem;
}
@media (min-width: 600px) {
  .contenido_derecho img {
    border-radius: 5rem;
    max-width: 50rem;
  }
}

.articulo_secundario {
  display: grid;
  grid-template-columns: 1fr;
  margin: 2rem;
  gap: 2rem;
  padding: 2rem;
  background-image: url(/imagenes/donacion2.jpg);
  background-position: center;
  background-size: cover;
  border-radius: 1.5rem;
  background-repeat: no-repeat;
}
@media (min-width: 1100px) {
  .articulo_secundario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.articulo_secundario_izquierda {
  display: grid;
  grid-template-columns: 1fr;
  flex-direction: row;
  border-radius: 1.5rem;
  gap: 2rem;
}
@media (min-width: 750px) {
  .articulo_secundario_izquierda {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.articulo_secundario_izquierda h2 {
  margin-top: 1rem;
}
.articulo_secundario_izquierda .contenido_secundario_articulo1 {
  background-color: #8b6666;
  padding: 1rem;
  border-radius: 1.5rem;
}
.articulo_secundario_izquierda .contenido_secundario_articulo1 li {
  color: white;
  margin-bottom: 1rem;
  line-height: 2rem;
}
.articulo_secundario_izquierda .contenido_secundario_articulo2 {
  background-color: rgb(230, 230, 168);
  padding: 1rem;
  border-radius: 1.5rem;
}
.articulo_secundario_izquierda .contenido_secundario_articulo2 p {
  font-size: 1.6rem;
  line-height: 2rem;
  text-align: center;
}
.articulo_secundario_izquierda .contenido_secundario_articulo2 img {
  max-width: 30rem;
}
.articulo_secundario_izquierda .contenido_secundario_articulo2 .corazonp {
  display: flex;
  justify-content: center;
  align-items: center;
}

.articulo_secundario_derecha {
  background-color: rgb(131, 236, 100);
  border-radius: 1.5rem;
}
.articulo_secundario_derecha h3 {
  margin-top: 2rem;
}
.articulo_secundario_derecha video {
  max-width: 100%;
  padding: 2rem;
}
.articulo_secundario_derecha .contenido_secundario_articulo iframe {
  padding: 2rem;
  width: 100%;
  height: 50rem;
  min-height: 10rem;
}

.articulo_tres {
  display: grid;
  grid-template-columns: 1fr;
  background-color: rgb(52, 111, 189);
  margin: 2rem;
  border-radius: 1.5rem;
  gap: 2rem;
  padding: 2rem;
}
@media (min-width: 750px) {
  .articulo_tres {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.articulo_tres .articulo_izquierda_tres .contenido_articulo_tres h3 {
  text-align: start;
}
.articulo_tres .articulo_izquierda_tres .contenido_articulo_tres p {
  color: white;
  line-height: 3rem;
  margin-bottom: 2rem;
}
.articulo_tres .articulo_izquierda_tres .contenido_articulo_tres a {
  font-weight: bold;
  color: white;
  border-bottom: 1px solid white;
}
.articulo_tres .articulo_derecha_tres .contenido_articulo_tress {
  background-color: rgb(97, 86, 72);
  padding: 2rem;
  border-radius: 1.5rem;
}
.articulo_tres .articulo_derecha_tres .contenido_articulo_tress li {
  color: white;
  line-height: 3rem;
}
.articulo_tres .articulo_derecha_tres .contenido_articulo_tress p {
  color: white;
  font-weight: bold;
}

